import React, { useEffect } from 'react';
import styled from 'styled-components';
import Matter from 'matter-js';

const GyroContainer = styled.div`
	width: 100vw;
	height: 100vh;
`;

const Gyro = () => {
	useEffect(() => {
		const {
			Composites,
			Engine,
			Render,
			Body,
			Runner,
			Common,
			Mouse,
			MouseConstraint,
			Composite,
			Bodies,
		} = Matter;

		// create engine
		const engine = Engine.create();
		const world = engine.world;

		// create renderer
		const render = Render.create({
			element: document.body,
			engine: engine,
			options: {
				width: window.innerWidth,
				height: window.innerHeight,
				showAngleIndicator: false,
				wireframes: false,
			},
		});

		Render.run(render);

		// create runner
		const runner = Runner.create();
		Runner.run(runner, engine);

		// add bodies
		const stack = Composite.create();
		Composite.add(
			stack,
			Composites.stack(50, 50, 30, 25, 0, 0, (x, y) => {
				const sides = Math.round(Common.random(1, 8));
				let chamfer = null;

				if (sides > 2 && Common.random() > 0.7) {
					chamfer = { radius: 10 };
				}

				switch (Math.round(Common.random(0, 1))) {
					case 0:
						return Bodies.rectangle(
							x,
							y,
							Common.random(10, 20),
							Common.random(10, 20),
							{
								chamfer,
							}
						);
					case 1:
						return Bodies.polygon(x, y, sides, Common.random(10, 30), {
							chamfer,
						});
					default:
						return null;
				}
			})
		);

		const windowWidth = window.innerWidth;
		const windowHeight = window.innerHeight;

		// Create an array to store the falling rectangles
		const fallingRectangles = [];

		// Function to create a falling rectangle
		const createFallingRectangle = () => {
			const rectangle = Bodies.rectangle(
				Common.random(windowWidth),
				0,
				Common.random(50, 100),
				Common.random(25, 50),
				{
					restitution: 0.5,
					render: {
						fillstyle: 'blue',
						strokeStyle: 'black',
						lineWidth: 1,
					},
				}
			);
			fallingRectangles.push(rectangle);
			Composite.add(world, rectangle);
		};

		// Function to reset a falling rectangle
		const resetFallingRectangle = (rectangle) => {
			Body.setPosition(rectangle, { x: Common.random(windowWidth), y: 0 });
			Body.setVelocity(rectangle, { x: 0, y: 0 });
		};

		// Initial creation of falling rectangles
		// for (let i = 0; i < 10; i++) {
		// 	createFallingRectangle();
		// }

		Composite.add(world, [
			stack,
			// Centered rectangles
			Bodies.rectangle(windowWidth / 2, 0, windowWidth, 50, { isStatic: true }),
			Bodies.rectangle(windowWidth / 2, windowHeight, windowWidth, 50, {
				isStatic: true,
			}),
			Bodies.rectangle(windowWidth, windowHeight / 2, 50, windowHeight, {
				isStatic: true,
			}),
			Bodies.rectangle(0, windowHeight / 2, 50, windowHeight, {
				isStatic: true,
			}),
		]);

		// add mouse control
		const mouse = Mouse.create(render.canvas);
		const mouseConstraint = MouseConstraint.create(engine, {
			mouse,
			constraint: {
				stiffness: 0.2,
				render: {
					visible: false,
				},
			},
		});

		Composite.add(world, mouseConstraint);

		// keep the mouse in sync with rendering
		render.mouse = mouse;

		// fit the render viewport to the scene
		Render.lookAt(render, {
			min: { x: 0, y: 0 },
			max: { x: window.innerWidth, y: window.innerHeight },
		});

		// add gyro control
		const updateGravity = (event) => {
			const orientation =
				typeof window.orientation !== 'undefined' ? window.orientation : 0;
			const gravity = engine.gravity;

			if (orientation === 0) {
				gravity.x = Common.clamp(event.gamma, -90, 90) / 90;
				gravity.y = Common.clamp(event.beta, -90, 90) / 90;
			} else if (orientation === 180) {
				gravity.x = Common.clamp(event.gamma, -90, 90) / 90;
				gravity.y = Common.clamp(-event.beta, -90, 90) / 90;
			} else if (orientation === 90) {
				gravity.x = Common.clamp(event.beta, -90, 90) / 90;
				gravity.y = Common.clamp(-event.gamma, -90, 90) / 90;
			} else if (orientation === -90) {
				gravity.x = Common.clamp(-event.beta, -90, 90) / 90;
				gravity.y = Common.clamp(event.gamma, -90, 90) / 90;
			}
		};

		window.addEventListener('deviceorientation', updateGravity);

		// cleanup on unmount
		return () => {
			Matter.Render.stop(render);
			Matter.Runner.stop(runner);
			if (typeof window !== 'undefined') {
				window.removeEventListener('deviceorientation', updateGravity);
			}
		};
	}, []); // Run once on mount

	return <GyroContainer />;
};

export default Gyro;
